<template>        
    <div id="1" class="card" style="width: 60%; margin: 0 auto; padding: 30px 30px;">
        <h3>6. 要保日期 ?</h3>
        <h4>( 註：保險契約簽約日期 )</h4>
    </div>
    <br>
    <div class="card" style="width: 60%; margin: 0 auto; padding: 30px 30px;">
        <div class="card-body">
            <div>
                <div class="input-group mb-3">
                    <input type="date" v-model="date" class="form-control">
                    <div class="input-group-append">
                        <button @click="press" class="btn btn-outline-success" type="button">
                            <b>確認</b>
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data (){
      return {    
        date: null
      }
    },
    methods: {
        press: function(){
            if(this.date == null){
                alert("請選擇要保日期");
            }
            else{
                this.$router.push({ 
                    name: '7',
                    params:{
                        name: this.$route.params.name,
                        type: this.$route.params.type,
                        samePeople: this.$route.params.samePeople,
                        insurerCareer: this.$route.params.insurerCareer,
                        insuredCareer: this.$route.params.insuredCareer,
                        insurerDate: this.date
                    }
                });
            }
        }
    }
};
</script>